<template>
  <div class="home_main">

    <!--<div class="slider">-->
      <!--<slider> </slider>-->
    <!--</div>-->

    <el-container >
      <el-header>
        <el-row class="block-col-2" type="flex">
          <el-col >
            <span class="title">管理后台</span>
            <el-button class="open" @click="handleSubmenu">展开</el-button>

          </el-col>
          <el-col  >
            <el-dropdown class="dropdown" @command="handleCommand">
  <span class="el-dropdown-link">
    {{userData.name}}<i class="el-icon-arrow-down el-icon--right"></i>
  </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="a">个人信息</el-dropdown-item>
                <el-dropdown-item command="b">退出登录</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-col>
        </el-row>
      </el-header>

      <el-container>
        <transition name="slider1">
          <div class="modal" v-show="openSubmenu" @click="closeModal"></div>
        </transition>
        <transition name="slider">
          <el-aside id="elaside"  v-show="openSubmenu">
            <slider :isCollapse1="collapse" @changeWid="changeWidth" @restoreWid="restoreWidth" ></slider>
          </el-aside>
        </transition>
        <el-main >
            <el-row>
                <router-view></router-view>
            </el-row>
        </el-main>


      </el-container>

    </el-container>
  </div>
</template>

<script>
    import slider from './main-components/slider'
    import Cookies from 'js-cookie'
    import axios from 'axios'
    export default {
        name: "home_main",
        components: {
          slider,
        },
        data() {
            return {
              userId:'',
              userData:{},
              openedList:'',
              collapse: false,
              showSlider: true,
              openSubmenu: false,
            }
        },
      mounted() {
          let vm = this;
          this.restoreWidth();
          if ( document.documentElement.clientWidth > 480 ){
                this.openSubmenu = true;
          }
          if (document.documentElement.clientWidth < 840){
            this.changeWidth();
            vm.collapse = true;
          }
          window.onresize = function () {
            if (document.documentElement.clientWidth > 840){
              this.openSubmenu = true;
              vm.collapse = false;
              vm.restoreWidth();
            }
            if (document.documentElement.clientWidth <640){
              vm.collapse =  true;
              vm.changeWidth();
            }
          };
          if (this.$route.params.userId) {
            this.userId = this.$route.params.userId;
            localStorage.setItem('userId',this.userId);
          }
          this._initUserData();

      },
      methods: {
        toggleClick(){
            this.collapse = !this.collapse;
            console.log(this.collapse);
        },
          // 更改aside宽度
        changeWidth(){
          let aside = document.getElementById('elaside');
          aside.style.width = '64px';

        },
        restoreWidth(){
          let aside = document.getElementById('elaside');
          aside.style.width = '201px';

        },
        // 获取个人 信息
        _initUserData(){
          let userId = localStorage.getItem('userId');
          console.log(userId);
          axios.post('/users/again',{
            userId: userId
          }).then( (response ) =>{
            let res = response.data;
            if (res.status === '0') {
              localStorage.setItem('userId',res.result.userId);
              this.userData = res.result;
            }else {
              alert("登录过期了!请重新登录");
              this.$router.push('/');
            }
          })
        },
        // 监听 路由 变化
        getPath(to,from,next) {
          if (document.documentElement.clientWidth <= 480){
            this.openSubmenu = false;
          }
          this.openedList = to.name;
          this.$store.commit('setOpenList',to.name);
        },
        // 处理下拉框点击事件
        handleCommand(command){
          if (command === 'b'){
            this.logout();
          }else {
            this.personInfo();
          }
        },
        // 查看个人信息
        personInfo(){
          this.$router.push({name: 'personInfo',params:{ userData: this.userData}});
        },
        // 退出登录
        logout(){
          Cookies.remove('userId');
          Cookies.remove('data');
          Cookies.remove('user');
          Cookies.remove('access');
          Cookies.remove('reportId');
          localStorage.clear();
          this.$router.push('/');
        },
        handleSubmenu() {
          this.openSubmenu = true;
        },
        closeModal() {
          this.openSubmenu = false;
        }
      },
      watch:{
          "$route": "getPath"
      }
    }
</script>

<style scoped >
  @media screen and (max-width: 480px) {
    #elaside{
      width: 64px !important;
      height: 100vh;
      position: absolute;
      z-index: 10;
    }
    .modal{
      position: absolute;
      z-index: 1;
      background: rgba(7,17,27,0.6);
      display:block;
      width: 100%;
      height: 100%;

    }
    .open{
      display: inline-block !important;
    }
  }
  .el-container{
    width: 100%;
    height: 100%;
  }
  .home_main{
    width: 100vw;
    height: 100vh;
    padding: 0;
    margin: 0 ;
    background: rgb(238,238,238);
  }
  .title{
    float: left;
    color: black;
  }
  .el-header {
    color: #fff;
    text-align: center;
    height: 50px !important;
    line-height: 50px;
    background: linear-gradient(to right ,#019d99,#25b7aa);
  }
  .mheader{
    margin-left: 10px;
    margin-top: 20px;
  }
  .el-main{
    padding:0 10px;
    background: #fff;

    min-height: 800px;
    height: 100%;
  }
  .el-dropdown{
    float: right;
    height: 50px;
    line-height: 50px;
  }
  #elaside{
    width: 201px ;
    background: rgb(61,65,80);
    overflow: hidden;
  }
  .open{
    display: none;
  }
  .el-icon-more{
    color: black;
  }
  .slider-enter-active{
    animation:  slider-into .4s;
  }
  .slider-leave-active{
    animation: slider-into .4s reverse;
  }
  .slider1-enter-active{
    animation:  slider-into .5s;
  }
  .slider1-leave-active{
    animation: slider-into .3s reverse;
  }
  @keyframes slider-into {
    0%{
      transform: translateX(-100%);
    }
    100%{
      transform: translateX(0);
    }
  }
</style>



