<template>
  <div class="slideMenu" id="slideMenu" style="width: 203px" >
    <!--<el-radio-group v-model="isCollapse" style="margin-bottom: 20px;">-->
      <!--<el-radio-button :label="islabel" @click="collapse">展开</el-radio-button>-->
      <!--<el-radio-button :label="!islabel" @click="collapse">收起</el-radio-button>-->
    <!--</el-radio-group>-->


    <span id="title">
      <el-button  id="btn" :style="{transform: 'rotateZ(' + (!this.isCollapse ? '-90' : '0') + 'deg)'}"
                  type="text" @click="toggleClick">
              <el-icon class="el-icon-more" ></el-icon>
      </el-button>
    </span>
    <el-menu  class="el-menu-vertical-demo"
      background-color="rgb(67,72,88)"  :router="true"  text-color="#fff"  active-text-color="rgb(250,200,57)"   @open="handleOpen" @close="handleClose"  :collapse="isCollapse">
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span slot="title">管理</span>
        </template>

        <el-menu-item-group>
          <el-menu-item index="/home_main/personManager">人员管理</el-menu-item>
          <el-menu-item index="/home_main/departManager">部门管理</el-menu-item>
        </el-menu-item-group>
      </el-submenu>

      <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-document"></i>
          <span slot="title">报告</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="/home_main/reportList">查看报告</el-menu-item>
          <el-menu-item index="/home_main/articlePublish">文章发布</el-menu-item>
          <el-menu-item index="/home_main/reportCheck">报告详情</el-menu-item>
        </el-menu-item-group>
      </el-submenu>

      <el-menu-item index="4">
        <i class="el-icon-setting"></i>
        <span slot="title">错误</span>
      </el-menu-item>
    </el-menu>

  </div>
</template>

<script>

    export default {
      components: {},
      name: "slider1",

      data() {
            return {
              showSlider: false,
              isCollapse: false
            }
      },
      props:[
        'isCollapse1'
      ],
      watch:{
        isCollapse1(newVal,oldVal){
          if (newVal){
            this.isCollapse = true;
            this.changeTitle('56px');
            this.$emit('changeWid');
          }else {
            this.isCollapse = false;
            this.changeTitle('200px');
            this.$emit('restoreWid');
          }
        }
      },
      mounted(){
        let vm = this;
        let width = document.documentElement.clientWidth;
        if (width <= 860){
          this.isCollapse = true;
          this.showSlider = false;
          this.changeTitle('56px');
          this.$emit('changeWid');
          if (width<=480){
            let dom = document.getElementById('title');
            dom.style.display = 'none';
          }

        }else {
          this.isCollapse = false;
          this.changeTitle('200px')
        }
        window.onresize = function () {
          let wid = document.documentElement.clientWidth;
          if (wid <= 860){
            vm.closeCollapse();
            vm.$emit('changeWid');
          }else {
            vm.openCollapse();
            vm.$emit('restoreWid');
          }
        }
      },
      methods:{
        handleOpen(key, keyPath) {
          console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
          console.log(key, keyPath);
        },
        toggleClick(){
          this.isCollapse = !this.isCollapse;
          if (this.isCollapse){
            this.changeTitle('56px');
            this.$emit('changeWid');
          }else {
            this.changeTitle('200px');
            this.$emit('restoreWid');
          }
        },
        closeCollapse(){
          this.isCollapse = true;
          this.changeTitle('56px');
          let slideMenu = document.getElementById('slideMenu');
        },
        openCollapse(){
          this.isCollapse = false;
          this.changeTitle('200px');
          this.$emit('restoreWid');
        },
        /* 更改旋转按钮的值*/
        changeTitle(width){
          let dom = document.getElementById('title');
          dom.style.width = width;
        }
      }

    }

</script>

<style scoped>
.slideMenu{
  overflow: hidden;
}
  #btn{
    height: 50px;
    color: #fff;
  }
  .el-button{
  }
  #title{
    display: block;
    color: darkgoldenrod;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }

</style>



